import React from 'react';
import { Outlet } from 'react-router-dom';
import Footer from '../components/home/footer';
import Navbar from '../components/navbar';

const Index = () => {
  return (
    <div>
      <Navbar />
      {/* React Router Outlet */}
      <Outlet />
      <Footer />
    </div>
  );
};

export default Index;
